.roll-radio [type="radio"]:checked,
.roll-radio[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
.roll-radio [type="radio"]:checked + label,
.roll-radio[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 24px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;

  color: #4a4a4a;
  font-family: Lato;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  margin-left: 5px;
}
.roll-radio [type="radio"]:checked + label:before,
.roll-radio[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 16px;
  height: 16px;
  border: 1px solid #2561a8;
  border-radius: 100%;
  background: #fff;
}
.roll-radio [type="radio"]:checked + label:after,
.roll-radio[type="radio"]:not(:checked) + label:after {
  content: "";
  width: 10px;
  height: 10px;
  background: #2561a8;
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  color: #2561a8;
}

.roll-radio [type="radio"]:checked + label {
  color: #2561a8;
}
.roll-radio [type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.roll-radio [type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

