.togbtn .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.togbtn .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.togbtn .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.togbtn .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.togbtn input:checked + .slider {
  background-color: #2196f3;
}

.togbtn input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

.togbtn input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.togbtn .slider.round {
  border-radius: 34px;
}

.togbtn .slider.round:before {
  border-radius: 50%;
}

.togbtn .switch input[type="checkbox"] {
  opacity: 0;
  position: absolute;
}
.togbtn .switch input[type="checkbox"] + .cr {
  position: relative;
  display: inline-block;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
  height: 20px;
  width: 35px;
  border: 1px solid #e9eaec;
  border-radius: 60px;
  cursor: pointer;
  z-index: 0;
  top: 5px;
  margin-left: 115px;
}
.togbtn .switch input[type="checkbox"] + .cr:after,
.togbtn .switch input[type="checkbox"] + .cr:before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
}
.togbtn .switch input[type="checkbox"] + .cr:before {
  -webkit-transition: 0.2s cubic-bezier(0.24, 0, 0.5, 1);
  transition: 0.2s cubic-bezier(0.24, 0, 0.5, 1);
  height: 20px;
  width: 35px;
  border-radius: 30px;
  background: #0091ff;
